* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
li {
  list-style: none;
}
.hd {
  background-color: darkturquoise;
  padding: 1rem;
  font-size: 2rem;
  color: white;
  text-align: center;
}
.list {
  max-width: 500px;
  margin: auto;
}
.list > li {
  border-bottom: 1px dotted gainsboro;
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.list > li > div:first-of-type > * {
  display: block;
}
.list > li > div:last-of-type {
  display: flex;
  align-items: center;
}
.date {
  font-size: 0.75rem;
  color: rgb(145, 145, 145);
}
.btn_good {
  outline: none;
  cursor: pointer;
  background-color: transparent;
  border: none;
  padding: 1rem;
}

/* modal */
.modal {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
}
.modal .con {
  background-color: white;
  padding: 1rem;
  border-radius: 1rem;
  min-height: 300px;
  width: 50%;
  max-width: 500px;
  position: relative;
}
.modal h1 {
  text-align: center;
}
.modal p {
  padding: 1rem 0;
}
.modal .close {
  position: absolute;
  top: 0;
  right: -50px;
}
